import {genName} from "@/util/string";
import {randInt} from "@/util/number";
import {compArr, compObj} from "@/util/object";

const ITEM_TYPES = {
    PLANT: 'plant',
    SEED: 'seed',
    COIN: 'coin',
    // MUTAGEN: 'mutagen',
    // FERTILIZER: 'fertilizer',
};

const RULES = {
    [ITEM_TYPES.PLANT]: {
        name: '植物',
        onUse(plant, slot) {
            if (!slot.plant) {
                slot.plant = plant;
                return 1;
            }
            return 0;
        },
        canPile(item, another) {
            return (
                item.gene === another.gene &&
                item.status === another.status &&
                item.value === another.value &&
                item.age === another.age &&
                item.seed === another.seed &&
                item.alive === another.alive
            );
        },
        onEstimate(plant) {
            return plant.value;
        },
    },
    [ITEM_TYPES.SEED]: {
        name: '种子',
        onUse(seed, slot) {
            if (!slot.plant) {
                slot.plant = makePlant(seed.gene);
                return 1;
            }
            return 0;
        },
        canPile(item, another) {
            return compArr(item.gene, another.gene) && compObj(item, another, 'status', 'value', 'age', 'seed', 'alive');
        },
        onEstimate(seed) {
            return seed.value;
        },
    },
    [ITEM_TYPES.COIN]: {
        name: '钱币',
        canPile() {
            return true;
        },
        onEstimate() {
            return 1;
        },
    },
    // [ITEM_TYPES.MUTAGEN]: {name: '诱变剂',},
    // [ITEM_TYPES.FERTILIZER]: {name: '肥料',},
};

function makeStack(item, amount = 1) {
    return {item, amount};
}

function makeSeed(gene, value = 0) {
    return {
        type: ITEM_TYPES.SEED,
        name: 'Seed of ' + nameOf(gene),
        gene: gene,
        value: value,
    };
}

function makePlant(gene) {
    return {
        type: ITEM_TYPES.PLANT,
        name: nameOf(gene),
        gene: gene,
        status: 0,
        value: 0,
        age: 0,
        seed: 0,
        alive: true,
    };
}

function nameOf(gene) {
    return genName(gene);
}

function run(plant) {
    if (!plant.alive) {
        return;
    }
    let gene = plant.gene;
    for (let i = 0; i < gene.length;) {
        switch (gene[i]) {
            case 1: {
                while (++i < gene.length && randInt(0x0F) <= gene[i]) {
                    plant.status++;
                }
            } break;
            case 2: {
                while (++i < gene.length && randInt(0x0F) <= gene[i]) {
                    plant.value++;
                }
            } break;
            case 3: {
                if (plant.status >= 0xFF) {
                    plant.status--;
                    plant.seed++;
                }
                ++i;
            } break;
            case 4: {
                let limit =  0;
                while (++i < gene.length) {
                    limit += gene[i];
                    if (gene[i] < 0x0F / 4) {
                        break;
                    }
                    if (randInt(plant.age) > limit) {
                        plant.dead = true;
                        break;
                    }
                }
            } break;
        }
    }
    plant.age++;
}

function harvest(plant) {
    let products = [
        makeStack(makeSeed(plant.gene, Math.floor(plant.value / plant.age * 0.5)), plant.seed),
    ];
    products.coins = plant.value;
    return products;
}

function estimate(item) {
    let rule = RULES[item.type];
    if (rule && rule.onEstimate) {
        return rule.onEstimate(item);
    }
    return null;
}

function canPile(stack1, stack2) {
    return stack1 !== null && stack1 === stack2 && stack1.type === stack2.type && RULES[stack1.type].canPile(stack1, stack2);
}

function putInto(storage, ...itemStacks) {
    for (let itemStack of itemStacks) {
        let stack = storage.find(s => canPile(s, itemStack));
        if (stack) {
            stack.amount += itemStack.amount;
        } else {
            storage.push(itemStack);
        }
    }
}

function takeFrom(storage, index, amount) {
    if (index >= 0 && index < storage.length) {
        let stack = storage[index];
        if (stack.amount >= amount) {
            stack.amount -= amount;
            if (!stack.amount) {
                let [ret] = storage.splice(index, 1);
                return ret;
            }
            return makeStack(Object.assign({}, stack.item), amount);
        }
    }
    return null
}

export {
    nameOf,
    makeSeed,
    makeStack,
    makePlant,
    RULES,
    ITEM_TYPES,
    run,
    harvest,
    estimate,
    putInto,
    takeFrom,
};