<template>
    <Card class="storage-item" :body-style="{padding: 0, margin: 0}" @click="click">
        <div class="storage-item-display">
            <p class="storage-item-name">{{ amount }} * {{ name }}</p>
            <ButtonGroup v-if="buttons">
                <Button @click="sell">出售</Button>
            </ButtonGroup>
        </div>
    </Card>
</template>

<script>
    import {Card, Button} from 'ant-design-vue';
    import {mapMutations} from 'vuex';
    import {RULES} from "@/model/plant";

    export default {
        name: "StorageItemStack",
        components: {Card, Button,
            ButtonGroup: Button.Group,
        },
        props: {
            index: {
                type: Number,
                required: true,
            },
            item: {
                type: Object,
            },
            amount: {
                type: Number,
                required: true,
            },
            buttons: {
                type: Boolean,
                required: false,
                default: () => true
            },
        },
        computed: {
            name() {
                return this.item.name || RULES[this.item.type].name;
            },
        },
        methods: {
            click() {
                this.$emit('click');
            },
            ...mapMutations(['sell']),
        },
    }
</script>

<style scoped>

    .storage-item {
        margin: 0;
        padding: 0;
    }
    .storage-item-display {
        margin: 0;
        padding: .5em 1.5em;
        text-align: left;
        font-family: Consolas, sans-serif;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    .storage-item-name {
        margin: 0;
        padding: 0;
        flex: 1;
        overflow: hidden;
        text-overflow: ellipsis;
    }
</style>