<template>
    <Card class="farm-slot" :body-style="{padding: 0, margin: 0}" @click="focus = true" @mouseleave="focus = false">
        <div class="plant-display">
            <div v-if="plant">
                <p>{{ plant.name }}</p>
                <Progress strokeLinecap="square" color="#66ccff" :percent="plant.status / 0xFF * 100"/>
            </div>
            <p v-else>空</p>
        </div>
        <ButtonGroup v-if="focus">
            <Button @click="harvest(index)">收获</Button>
            <Button @click="collect(index)">回收</Button>
            <Button @click="use">使用</Button>
        </ButtonGroup>
    </Card>
</template>

<script>
    import {Card, Button, Progress} from 'ant-design-vue';
    import {mapMutations} from 'vuex';

    export default {
        name: "FarmSlot",
        components: {Card, Button, Progress,
            ButtonGroup: Button.Group,
        },
        props: {
            index: {
                type: Number,
                required: true,
            },
            plant: {
                type: Object,
                default: () => null,
            },
        },
        data() {
            return {
                focus: false,
            };
        },
        methods: {
            use() {
                this.$emit('use', this.index);
            },
            ...mapMutations(['harvest', 'collect']),
        },
    }
</script>

<style scoped>
    .farm-slot {
        margin: 0;
        padding: 0;
    }
    .plant-display > * {
        width: 100%;
        margin: 0;
        padding: .5em 1.5em;
        text-align: left;
        font-family: Consolas, sans-serif;
    }
</style>