import {estimate, harvest, makePlant, makeSeed, makeStack, putInto, RULES, run, takeFrom} from "@/model/plant";
import {randArr} from "@/util/number";


export default {
    state: {
        coins: 5000,
        slots: [],
        storage: [],
        pid: null,
    },
    mutations: {
        harvest(state, index) {
            let plant = state.slots[index].plant;
            if (plant) {
                let products = harvest(plant);
                putInto(state.storage, ...products);
                state.coins += products.coins || 0;
            }
        },
        collect({slots, storage}, index) {
            let plant = slots[index].plant;
            if (plant) {
                putInto(storage, makeStack(plant));
            }
        },
        sell(state, index) {
            let stack = takeFrom(state.storage, index, 1);
            if (stack) {
                let value = estimate(stack.item);
                if (value !== null) {
                    state.coins += value;
                } else {
                    putInto(state.storage, stack);
                }
            }
        },
        buySeed(state) {
            if (state.coins >= 100) {
                state.coins -= 100;
                let seed = makeSeed(randArr(50, 20, 0x0F), 25);
                putInto(state.storage, makeStack(seed));
            }
        },
        buySlot(state) {
            if (state.coins >= 1000) {
                state.coins -= 1000;
                state.slots.push({index: state.slots.length, plant: null});
            }
        },
        useAt({slots, storage}, {slotIndex, storageIndex}) {
            let slot = slots[slotIndex];
            let stack = storage[storageIndex];
            let rule = RULES[stack.item.type];
            if (rule && rule.onUse) {
                let cost = rule.onUse(stack.item, slot);
                if (cost) {
                    takeFrom(storage, storageIndex, cost);
                }
             }
        },
        createNewPlant({storage}) {
            let plant = makePlant(Array(10).fill(0).map(() => Math.floor(Math.random() * 0x0F)));
            putInto(storage, makeStack(plant));
        },
        tick({slots}, dt) {
            slots.forEach(slot => {
                if (slot.plant) {
                    for (let i = 0; i < dt; i++) {
                        run(slot.plant)
                    }
                }
            });
        },
        setPid(state, pid) {
            state.pid = pid;
        },
        fillState(state, newState) {
            Object.assign(state, newState);
        }
    },
    actions: {
        start({dispatch, commit, state}) {
            dispatch('load');
            if (state.pid !== null) {
                clearInterval(state.pid);
            }
            let pid = setInterval(() => {
                commit('tick', 1);
                dispatch('save');
                }, 60000);
            commit('setPid', pid);
        },
        stop({dispatch, commit, state}) {
            if (state.pid !== null) {
                clearInterval(state.pid);
                commit('setPid', null);
            }
            dispatch('save');
        },
        load({commit}) {
            let data = JSON.parse(localStorage.getItem('n-farm-data')) || {};
            commit('fillState', data);
        },
        save({state}) {
            let data = JSON.stringify({
                coins: state.coins,
                slots: state.slots,
                storage: state.storage,
            });
            localStorage.setItem('n-farm-data', data);
        },
        clear({commit}) {
            let data = JSON.stringify({
                coins: 0,
                slots: [],
                storage: [],
            });
            commit('fillState', data);
            localStorage.removeItem('n-farm-data');
        },
    },
};