<template>
    <div class="slots">
        <Modal
                v-model="selectingItem"
                class="item-picker-wrapper"
                title="选择物品"
                @ok="confirmSelectItem"
                @cancel="cancelSelectItem"
        >
            <div class="item-picker">
                <StorageItemStack
                        v-for="(stack, index) of storage"
                        :class="(selectedStorageIndex === index) ? 'selected' : ''"
                        :key="index"
                        :index="index"
                        :item="stack.item"
                        :amount="stack.amount"
                        :buttons="false"
                        @click="pickItem(index)"
                />
            </div>
        </Modal>
        <FarmSlot class="slot" v-for="slot of slots" :index="slot.index" :key="slot.index" :plant="slot.plant" @use="selectItem"/>
    </div>
</template>

<script>
    import {Modal} from 'ant-design-vue';
    import FarmSlot from "@/components/FarmSlot";
    import {mapState, mapMutations} from 'vuex';
    import StorageItemStack from "@/components/StorageItemStack";

    export default {
        name: "Farm",
        components: {StorageItemStack, FarmSlot, Modal},
        data() {
            return {
                selectingItem: false,
                selectedStorageIndex: -1,
                selectedSlotIndex: -1,
            }
        },
        computed: {
            ...mapState(['slots', 'storage']),
        },
        methods: {
            selectItem(slotIndex) {
                this.selectedSlotIndex = slotIndex;
                this.selectingItem = true;
            },
            pickItem(storageIndex) {
                this.selectedStorageIndex = storageIndex;
            },
            confirmSelectItem() {
                if (this.selectedSlotIndex >= 0 && this.selectedStorageIndex >= 0) {
                    this.$store.commit('useAt', {
                        slotIndex: this.selectedSlotIndex,
                        storageIndex: this.selectedStorageIndex,
                    });
                }
                this.selectedSlotIndex = -1;
                this.selectedStorageIndex = -1;
                this.selectingItem = false;
            },
            cancelSelectItem() {
                this.selectedSlotIndex = -1;
                this.selectedStorageIndex = -1;
                this.selectingItem = false;
            },
            ...mapMutations(['createNewPlant']),
        },
    }
</script>

<style scoped>
    .slots {
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .slot {
        margin: .5em 1em;
        padding: .5em 1em;
    }
    .item-picker {
        height: 100%;
        padding: 1em;
    }
    .item-picker > * {
        margin: .5em 0 ;
    }
    .selected {
        border: .1em solid #7eb2e6;
    }
</style>