<template>
  <Layout id="app">
    <Menu class="menu" theme="dark" mode="horizontal" :default-selected-keys="menuKeys" v-model="menuKeys" @click="changePage">
      <MenuItem key="page-home">主页</MenuItem>
      <MenuItem key="page-farm">农场</MenuItem>
      <MenuItem key="page-storage">仓库</MenuItem>
    </Menu>
    <Content class="page-content">
      <component :is="component"/>
    </Content>
  </Layout>
</template>

<script>
  import {Menu, Layout} from 'ant-design-vue';
  import Farm from "@/components/Farm";
  import Storage from "@/components/Storage";
  import HomePage from "@/components/HomePage";

  export default {
    name: 'App',
    components: {Farm, Menu, Layout, Storage,
      MenuItem: Menu.Item,
      Content: Layout.Content,
      Header: Layout.Header,
    },
    data() {
      return {
        page: 'page-home',
        menuKeys: ['page-home'],
      }
    },
    computed: {
      component() {
        switch (this.page) {
          case "page-farm": return Farm;
          case "page-storage": return Storage;
          case "page-home": return HomePage;
          default: return Farm;
        }
      },
    },
    watch: {
      page(page) {
        this.menuKeys[0] = page;
      },
    },
    methods: {
      changePage({key}) {
        if (key.startsWith('page-')) {
          this.page = key;
        }
      }
    },
    mounted() {
      this.$store.dispatch('start');
    },
    beforeDestroy() {
      this.$store.dispatch('stop');
    },
  }
</script>

<style>
  html, body, #app {
    width: 100%;
    height: 100%;
    margin: 0;
    box-sizing: border-box;
  }

  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  .page-content {
    height: 100%;
    overflow-y: hidden;
  }
</style>
