<template>
    <Layout class="home-page">
        <p>硬币：{{ coins }}</p>
        <Button block @click="buySeed">购买普通品质种子</Button>
        <Button block @click="buySlot">购买一块土地</Button>
        <Button block @click="load">手动载入</Button>
        <Button block @click="save">手动保存</Button>
        <Button block @click="clear">清空存档</Button>
        <Button block v-if="pid === null" @click="start">开始Tick</Button>
        <Button block v-else @click="stop">停止tick</Button>
    </Layout>
</template>

<script>
    import {Layout, Button} from 'ant-design-vue';
    import {mapState, mapMutations, mapActions} from 'vuex';

    export default {
        name: "HomePage",
        components: {Layout, Button},
        computed: {
            ...mapState(['coins', 'pid']),
        },
        methods: {
            ...mapMutations(['buySeed', 'buySlot']),
            ...mapActions(['start', 'stop', 'load', 'save', 'clear']),
        },
    }
</script>

<style scoped>
    .home-page {
        margin: 0;
        padding: 0 1em;
    }
    .home-page > * {
        margin-top: 1em;
    }
</style>