<template>
    <div class="storage">
        <StorageItemStack
                class="item"
                v-for="(stack, index) of storage"
                :key="index"
                :index="index"
                :item="stack.item"
                :amount="stack.amount"
        />
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import StorageItemStack from "@/components/StorageItemStack";

    export default {
        name: "Storage",
        components: {StorageItemStack},
        computed: {
            ...mapState(['storage']),
        },
    }
</script>

<style scoped>
    .storage {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .item {
        margin: .5em 1em;
        padding: .5em 1em;
    }
</style>