function randInt(max = 0x0F, min = 0) {
    return Math.floor(Math.random() * (max - min) + min)
}

function randArr(lenMax = 0x0F, lenMin = 0, valMax = 0x0F, valMin = 0) {
    return new Array(lenMax - lenMin).fill(0).map(() => randInt(valMax, valMin));
}

export {
    randInt,
    randArr,
}